/* START GENERAL CSS */
*,
*::after,
*::before {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html, body {
  height: 100%;
  margin: 0;
}

body {
  font: 18px/1.3 "DM Sans", sans-serif;
  color: #737B7D;
  min-width: 320px;
  letter-spacing: 0.3px;
  background: #fff;
}

/* General reset */
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* General reset jcf */
form, fieldset {
  margin: 0;
  padding: 0;
  border-style: none;
}

input,
select,
textarea,
select,
button {
  vertical-align: middle;
  font: 18px/1.3 "DM Sans", sans-serif;
}

button,
input {
  *overflow: visible;
  line-height: normal;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border: 0;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  cursor: pointer;
  -webkit-appearance: button;
}

label,
select,
button,
input[type=button],
input[type=reset],
input[type=submit],
input[type=radio],
input[type=checkbox] {
  cursor: pointer;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

/* General reset html 5 */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

figure {
  margin: 0;
}

/* General reset */
img {
  border: 0;
  -ms-interpolation-mode: bicubic;
}

a {
  text-decoration: underline;
}
a:hover {
  text-decoration: none;
}
a:focus, a:hover {
  outline: none;
  outline: 0;
}

p {
  margin: 0 0 18px;
}

input[type=tel],
input[type=text],
input[type=email],
input[type=password] {
  width: 100%;
  height: 45px;
  padding: 12px;
  margin: 0 0 6px;
  outline: none;
  outline: 0;
  box-sizing: border-box;
  border: 1px solid #000;
  border-radius: 8px;
  box-shadow: none;
  -webkit-appearance: none;
  transition: all 0.3s ease;
  position: relative;
  font-size: 18px;
  line-height: 18px;
  color: #737B7D;
  font-weight: 400;
  text-align: left;
  outline: none;
  outline: 0;
  overflow: hidden;
}
input[type=tel]::placeholder,
input[type=text]::placeholder,
input[type=email]::placeholder,
input[type=password]::placeholder {
  opacity: 1; /* Firefox */
}
input[type=tel]:-ms-input-placeholder,
input[type=text]:-ms-input-placeholder,
input[type=email]:-ms-input-placeholder,
input[type=password]:-ms-input-placeholder { /* Internet Explorer 10-11 */ }
input[type=tel]::-ms-input-placeholder,
input[type=text]::-ms-input-placeholder,
input[type=email]::-ms-input-placeholder,
input[type=password]::-ms-input-placeholder { /* Microsoft Edge */ }
textarea {
  overflow: auto;
  vertical-align: top;
  border-radius: 8px;
  border: 1px solid #000;
  width: 100%;
  padding: 12px;
  margin: 0 0 6px;
  outline: none;
  outline: 0;
  min-height: 172px;
  max-width: 100% !important;
  min-width: 100%;
  box-sizing: border-box;
  box-shadow: none;
  font-weight: 400;
  -webkit-appearance: none;
  transition: all 0.3s ease;
}
form {
  position: relative;
  margin: 0;
  width: 100%;
}
form input[type=button],
form button {
  border: none;
  background: #737B7D;
  text-align: center;
  width: 100%;
  font-weight: bold;
}
form label {
  width: 100%;
  display: block;
  position: relative;
}
form label:hover {
  cursor: default;
}
form input[readonly],
form button[readonly] {
  color: #737B7D !important;
  background: #737B7D !important;
  border: none !important;
}

h1, h2, h3, h4 {
  margin: 0 0 15px;
}

h1 {
  font-size: clamp(38px, 4vw, 64px);
  letter-spacing: -4px;
}

.container {
  --gutter: 16px;
  max-width: calc(1440px + var(--gutter) * 2);
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: var(--gutter);
  padding-right: var(--gutter);
}
@media (min-width: 768px) {
  .container {
    --gutter: 44px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  height: 56px;
  font-size: 15px;
  line-height: 1;
  font-weight: 600;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #fff;
  text-decoration: none;
  border: 2px solid transparent;
  padding: 5px 40px;
  transition: all 0.2s ease;
  background: #DA5D24;
  color: #fff;
  border-color: #DA5D24;
}
.btn--default:hover {
  background: color-mix(in srgb, #DA5D24, black 20%);
  border-color: color-mix(in srgb, #DA5D24, black 20%);
}
.btn--transparent {
  background: rgba(6, 37, 91, 0.8);
  color: #fff;
  border-color: #fff;
}
.btn--white {
  background: #fff;
  color: #06255B;
  border-color: #fff;
}
.btn--white:hover {
  background: color-mix(in srgb, #fff, black 20%);
  border-color: color-mix(in srgb, #fff, black 20%);
}

.wrapper {
  min-height: 100vh;
  overflow: hidden;
}

/*.intro-section*/
.intro-section {
  min-height: 100vh;
  padding: 100px 0;
  position: relative;
  display: flex;
  align-items: center;
  background-color: #06255B;
  color: #fff;
}
.intro-section__bg {
  position: absolute;
  inset: 0;
  background-position: 0 0;
  background-size: cover;
  background-repeat: no-repeat;
}
.intro-section__bg::before {
  content: "";
  position: absolute;
  inset: 0 auto 0 0;
  width: 45%;
  background: linear-gradient(90deg, #06255B 0%, rgba(6, 37, 91, 0) 100%);
}
.intro-section__bg::after {
  content: "";
  position: absolute;
  inset: 0 0 auto 0;
  height: 60%;
  background: linear-gradient(180deg, #06255B 0%, rgba(6, 37, 91, 0) 100%);
}
.intro-section__decor {
  position: absolute;
  top: 50%;
  right: -450px;
  transform: translate(0, -50%) rotate(45deg);
  width: 618px;
  aspect-ratio: 1;
  background: rgba(218, 93, 36, 0.8);
  z-index: 1;
}
.intro-section__decor::before {
  content: "";
  position: absolute;
  top: -20px;
  left: -20px;
  width: 100%;
  height: 100%;
  border: 1px solid #DA5D24;
}
.intro-section__decor::after {
  content: "";
  position: absolute;
  top: 20px;
  left: 20px;
  width: 100%;
  height: 100%;
  border: 1px solid #fff;
  z-index: -1;
}
.intro-section__row {
  display: flex;
  gap: 15px;
}
.intro-section__info {
  width: 50%;
}
.intro-section__additional {
  width: 40%;
  display: flex;
  justify-content: flex-end;
}
.intro-section .container {
  position: relative;
}
.intro-section h1 {
  font-weight: 400;
}
.intro-section p {
  max-width: 550px;
}
.intro-section .btn-group {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

.mosaic-box {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 45px 25px;
  align-items: end;
  max-width: 400px;
  width: 100%;
  transform: rotate(45deg);
  position: relative;
  z-index: 1;
}
.mosaic-box__item {
  background: rgba(6, 37, 91, 0.8);
  aspect-ratio: 1/1;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mosaic-box__item:nth-child(1) {
  grid-area: 1/1/1/1;
  width: 90%;
  border: 8px solid #fff;
  font-size: 21px;
}
.mosaic-box__item:nth-child(2) {
  grid-area: 2/1/2/1;
  width: 70%;
  border: 9px solid #06255B;
  font-size: 22px;
}
.mosaic-box__item:nth-child(3) {
  grid-area: 2/2/2/2;
  width: 100%;
  border: 10px solid #DA5D24;
  font-size: 21px;
}
.mosaic-box__item-label {
  transform: rotate(-45deg);
}

.servises-section {
  padding: 108px 0 200px;
  background-color: #06255B;
  color: #fff;
  position: relative;
}
.servises-section__info-inner {
  max-width: 355px;
  margin: 0 100px;
}
.servises-section__presentation {
  margin-top: -150px;
}

.servises-mosaic {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  max-width: 48vw;
  transform: rotate(45deg);
  transform-origin: 50% 100%;
  position: relative;
  margin-top: -30vw;
  margin-left: -6vw;
}
.servises-mosaic::before {
  content: "";
  width: 99999px;
  height: 99999px;
  background: #F7F9FC;
  position: absolute;
  bottom: 100%;
  left: calc(33.3333333333% + 4px);
  transform: translateY(-16px);
}
.servises-mosaic::after {
  content: "";
  width: 99999px;
  height: 99999px;
  background: #fff;
  position: absolute;
  top: 0;
  left: 28%;
  transform: translateY(-50%);
  z-index: -1;
}
.servises-mosaic__item {
  width: 100%;
  aspect-ratio: 1;
  background: #fff;
  text-decoration: none;
  color: #313131;
  text-align: center;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #d5d5d5;
  position: relative;
}
.servises-mosaic__item:nth-child(1) {
  grid-area: 1/2/1/2;
}
.servises-mosaic__item:nth-child(2) {
  grid-area: 2/2/2/2;
}
.servises-mosaic__item:nth-child(3) {
  grid-area: 2/3/2/3;
}
.servises-mosaic__item:nth-child(4) {
  grid-area: 3/2/3/2;
}
.servises-mosaic__item:nth-child(5) {
  grid-area: 4/1/4/1;
}
.servises-mosaic__item:nth-child(6) {
  grid-area: 4/2/4/2;
}
.servises-mosaic__item-label {
  transform: rotate(-45deg);
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.header {
  --currentColor: #fff;
  position: fixed;
  top: 20px;
  left: 0;
  right: 0;
  padding: 4px 0 4px 5vw;
  z-index: 999;
  color: var(--currentColor);
}
.header__container {
  display: flex;
  align-items: center;
}
.header__info {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  color: currentColor;
  display: flex;
  width: 19vw;
  max-width: 263px;
  width: clamp(218px, 19vw, 263px);
  margin: 0 10px 0 0;
}
.header__logo svg {
  width: 100%;
  height: auto;
}
.header__logo svg * {
  fill: currentColor;
}
.header__controls {
  display: flex;
  align-items: center;
  gap: 16px;
  margin: 0 0 0 10px;
}

.primary-nav {
  display: flex;
  flex-wrap: wrap;
  column-gap: 32px;
  row-gap: 5px;
}
.primary-nav__link {
  font-size: 15px;
  color: currentColor;
  text-decoration: none;
  letter-spacing: 0.2px;
}
@media (width < 1500px) {
  .primary-nav {
    column-gap: 10px;
  }
}

.lang-set {
  display: flex;
  align-items: center;
  gap: 12px;
  height: 56px;
  padding: 5px 19px 5px 15px;
  background: #fff;
}
.lang-set__item {
  display: block;
  padding: 0 0 0 18px;
  text-decoration: none;
  color: rgba(6, 37, 91, 0.4);
  background-position: 0 50%;
  background-repeat: no-repeat;
  background-size: 12px 12px;
  position: relative;
  transition: all 0.2s ease;
}
.lang-set__item:hover {
  color: #06255B;
}
.lang-set__item--de {
  background-image: url(../images/flag-de.svg);
}
.lang-set__item--en {
  background-image: url(../images/flag-en.svg);
}
.lang-set__item--fr {
  background-image: url(../images/flag-fr.svg);
}
.lang-set__item--active {
  color: #06255B;
  text-decoration: underline !important;
}